"use client"
import { useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

export default function AdminLoginPage() {
  const [password, setPassword] = useState("")
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const router = useRouter()

  const submit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)
    const res = await fetch("/api/admin/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ password }),
    })
    if (res.ok) {
      router.replace("/admin/hirek")
    } else {
      const msg = await res.text()
      setError(msg || "Hibás jelszó")
    }
    setLoading(false)
  }

  return (
    <div className="container mx-auto max-w-md px-4 py-12">
      <h1 className="font-serif text-2xl md:text-3xl font-bold mb-6">Admin belépés</h1>
      <form onSubmit={submit} className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="password">Jelszó</Label>
          <Input id="password" type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
        </div>
        {error && <p className="text-sm text-red-600">{error}</p>}
        <Button type="submit" className="bg-primary text-primary-foreground w-full" disabled={loading}>
          {loading ? "Belépés..." : "Belépés"}
        </Button>
      </form>
    </div>
  )
}
